@import 'variables';

@function calculatePixelToRem($size) {
    @return $size / 14 * 1rem;
}

@mixin font-size($size) {
    font-size: calculatePixelToRem($size);

    @if map-has-key($sizeLineHeightMap, $size) {
        line-height: calculatePixelToRem(map-get($sizeLineHeightMap, $size));
    }
}
